import React, { Component } from 'react';

/* accepts a PAGE object in arbitrary form. 
   PAGE should have a TITLE and DESCRIPTION properties in place. */

class SEO extends React.Component {
    componentDidMount() {
        let seo = this.props.page;
        document.title = seo.title;
        
        
        let head = document.getElementsByTagName('head')[0],
            description =  document.createElement('meta'),
            oldDescription = document.querySelector("meta[name='description']");
        
        if (oldDescription) {
            head.removeChild(oldDescription);
        }
        
        description.name = "description";
        description.content = seo.description;
        head.appendChild(description);
    }
    
    render() {
        return null;
    }
}


export default SEO;