import React, { Component } from 'react';
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from 'react-google-maps';
import './map.css';

const latLng = { lat: 47.7860706, lng: 11.8265243 };
const MapContainer = withScriptjs(withGoogleMap((props) => (
    <GoogleMap
        defaultZoom={ 10 }
        defaultCenter={{ lat: latLng.lat, lng: latLng.lng }}
        defaultOptions={ props.options }
        >
        { props.isMarkerShown && <Marker position={{ lat: latLng.lat, lng: latLng.lng }} />}
    </GoogleMap>
)));

const apiKey = 'AIzaSyAmYJYbmp21jWXueZH2wij8eNO0N8tUVmg';
