const Map = (props) => {
    let mapDrag = window.innerWidth >= 800;
    
    return (
        <MapContainer 
            isMarkerShown
              googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyAmYJYbmp21jWXueZH2wij8eNO0N8tUVmg"
              loadingElement={<div style={{ height: `100%` }} />}
              containerElement={<div style={{ height: `400px` }} />}
              mapElement={<div style={{ height: `100%` }} />}
              options= {{
                    disableDefaultUI: true,
                    zoomControl: true,
                    gestureHandling: 'greedy',
                    scrollwheel: false,
                    draggable: mapDrag
              }}
        />
    );
};

export { Map };