import React, { Component } from 'react';
import './slider.css';

class BigPicture extends Component {
    constructor(props) {
        super(props);
        
        this.expectedHeight = props.height;
        this.load = this.load.bind(this);
        this.state = { active: typeof props.active === 'boolean' ? props.active : true };
    }
    
    componentWillReceiveProps(newProps) {
        if (typeof newProps.active === 'boolean')
            this.setState({ active: newProps.active });
    }
    
    componentDidMount() {

    }
    
    componentWillUnmount() {

    }
    
    load() {
        const imgRef = this.imgRef,
              naturalW = imgRef.naturalWidth,
              naturalH = imgRef.naturalHeight,
              naturalRatio = naturalH / naturalW,
              expectedHeight = this.expectedHeight;
        
        let width = window.innerWidth,
            height = (width * naturalRatio);
        
        if (expectedHeight && height < expectedHeight) {
            height = this.expectedHeight;
            width = height / naturalRatio;
        }
        
        imgRef.style.width = `${ width }px`;
        imgRef.style.height = `${ height }px`;
    }

    render() {
        const width = window.innerWidth;
        const height = 550;
        
        let active = this.state.active,
            picture = <img 
                        className={ "BigPicture " + (active ? 'active' : 'inactive') }
                        ref= { (e) => this.imgRef = e }
                        src={ this.props.src }
                        alt={ this.props.alt }
                        style={{ 
                               width: width,
                               height: height,
                               left: '50%',
                               transform: 'translateX(-50%)'
                              }}
                        onLoad={ this.load }
                      />;

        if (this.props.wrapper) {
            return (
                <div className="relativeWrapper" style={ { height: this.expectedHeight || 550 } }>
                    { picture }
                </div>
            );
        } else {
            return picture;
        }

    }
    
}

class Slider extends Component {
    constructor(props) {
        super(props);
        
        this.images = props.pictures;
        
        this.state = {
            length: this.images.length,
            activePicture: 0
        }
    }
     
    tick() {
        this.setState((prevState) => {
           return { activePicture: (prevState.activePicture + 1) % prevState.length }
        });
    }
    
    componentDidMount() {        
        this.tickId = setInterval(
            () => this.tick(), 
            3500
        );
    }
    
    componentWillUnmount() {
        clearInterval(this.tickId);
    }
    
    render() {
        return (
            <div className="Slider" style={ {height: (this.props.height || 550)} }>
                { 
                    this.props.pictures.map(
                        (picture, index) => <
                            BigPicture 
                                key={ index } 
                                active={ this.state.activePicture === index } 
                                src={ picture.src } 
                                alt={ picture.alt } 
                                height= {  this.props.height || 550 }
                            />
                    )
                }
            </div>
        );
    }
};

export { BigPicture, Slider };