import React, { Component } from 'react';
import { HashLink as Link} from 'react-router-hash-link';
import './services.css';

class Services extends Component {
    constructor(props) {
        super(props);
    }
    
    render() {
        const services = this.props.services;
        
        return (
            <div className="Services">
                { 
                    services.map((service) => (
                        <Link smooth to={ `/leistungen#${ service.name }` }>
                            <div className="service">
                                <h1>{ service.name }</h1>
                            </div>
                        </Link>
                    ))
                }
            </div>
        );
    }
}

class ServiceList extends Component {
    render() {
        const services = this.props.services;
        
        return (
            <div className="ServiceList">
                {
                    services.map((service) => (
                        <div className="ServiceListItem">
                            <a className="anchor" id={ service.name }></a>
                            <div>
                                <div className="icon"><img src={ service.icon } alt={ service.alt || service.name } /></div>
                                <div className="text">
                                    <h1 className="title">{ service.title }</h1>
                                    { service.text }
                                </div>
                            </div>
                        </div>
                    ))
                }
            </div>
        );
    }
}

export { Services, ServiceList };