import React, { Component } from 'react';
import './preloader.css';

class Preloader extends Component {
    constructor(props) {
        super(props);
 
        this.state = {
          loading: true
        };
    }
    
    load() {
        const preloader = this.preLoader;
        preloader.className = "preLoader active";
        setTimeout(
            function() {
                preloader.className = "preLoader";
        }, 500);
    }
    
    componentDidMount() {
        this.load();
    }
    
    componentWillUpdate() {
        this.load();
    }
    
    componentDidUpdate(prevProps) {
        window.scrollTo(0, 0);
    }
    
    render() {
        return (
                <div 
                ref={(e) => this.preLoader = e} 
                className="preLoader active">
                    
                    <div className="wheel"></div>
        
                </div>
               );
    }
}
    
    
export default Preloader;